import React from "react";
import { Link } from 'react-router-dom';
import "./Ready.css";
import icon_cat from "../images/icon_cat.png";
import icon_dog from "../images/icon_dog.png";


const mapSize = {x: 20, y: 20};
const firstTeamIdx = 2;

class Ready extends React.Component {
	render() {
		return (
			// 게임 순서 정하기
			<div className="container__beforeStart">
				<p>누가 먼저 시작할래?</p>
				<div className="container__icons">
					<Link
					className="container__icon"
					to={{
						pathname: "/game",
						state: {
							mapSize,
							firstTeamIdx,
							nowTurn: firstTeamIdx
						}
						}}>
						<img className="choice__cat" src={icon_cat} alt="고양이" />
						<p>고양이</p>
					</Link>
					<Link
					className="container__icon"
					to={{
						pathname: "/game",
						state: {
							mapSize,
							firstTeamIdx,
							nowTurn: firstTeamIdx+1
						}
						}}>
						<img className="choice__dog" src={icon_dog} alt="강아지" />
						<p>강아지</p>
					</Link>
				</div>
			</div>
		)
	}
}

export default Ready;